import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { ProfileProvider } from '../../providers/profile/profile';
import { PopUpProvider } from '../../providers/pop-up/pop-up';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { DirectorProvider } from '../../providers/director/director';
import { ActivityProvider } from '../../providers/activity/activity';
import { NativeMapContainerProvider } from '../../providers/native-map-container/native-map-container';
import { SettingsProvider } from '../../providers/settings/settings';
import { StripeProvider } from '../../providers/stripe/stripe';
import { PaystackProvider } from '../../providers/paystack/paystack';

/**
 * Generated class for the BasketPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-basket',
  templateUrl: 'basket.html',
})
export class BasketPage {
  carts: any;
  public details: string = "menus";
  items: any;
  constructor(public gCode: GeocoderProvider, public modalCtrl: ModalController, public settings: SettingsProvider, public stripe: StripeProvider, public paystack: PaystackProvider, public cMap: NativeMapContainerProvider, public act: ActivityProvider, public director: DirectorProvider, public navCtrl: NavController, public pop: PopUpProvider, public ph: ProfileProvider, public navParams: NavParams) {
  }

  ionViewDidLoad() {
  
    this.ph.getUserProfile().child('Basket').on('value', userProfileSnapshot => {
      this.items = [];
      
      userProfileSnapshot.forEach( snap => {
     
           snap.forEach(element => {
             console.log(element.val())

            this.items.push({
              key: element.key,
              name: element.val().name,
              quantity: element.val().quantity,
              price: element.val().price,
              drink: element.val().drink,
              drinks: element.val().drinks,
              restuarantKey: element.val().restuarantKey,
            });
  
           
            console.log(this.items)
            return false
           });


          return false
      
        });
    })

  }


  onClick(item){
    console.log(item)
    this.ph.getUserProfile().child('Basket').child(item.key).remove().then(()=>{
       
    })
  
  
}

Accept(){
 // this.navCtrl.push('BasketPage')
 let drinks = []
 let foods = []
 let quantity = []
 
   
var today = new Date();
var date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();

let pay = '0'
 if (this.ph.card != null){

    if (this.settings.isStripe){
      this.stripe.ChargeCard(this.director.total, this.ph.id)
    }else{
  this.paystack.ChargeCard(this.ph.card, this.ph.month, this.ph.cvc, this.ph.year, this.director.total*100, this.ph.email, this.ph.id)
    }

    this.act.getActivityProfile(this.ph.id).once('value', userProfileSnapshot => {
      if (userProfileSnapshot.val())
        if (userProfileSnapshot.val().Client_hasPaid){
          this.items.forEach(element => {
            console.log(element.drinks)
            if (element.drinks)
               drinks.push(element.drinks);
             foods.push(element.name);
             console.log(element.restuarantKey);
             console.log(element.price)
             this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').on('value', snap=>{
              if(snap.val())
              pay = snap.val().Client_Total
            }) 
             if (element.drinks){
              this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').push({
                Client_Name: this.ph.name,
                Client_Location: this.gCode.locationName,
                Client_location: [this.cMap.lat, this.cMap.lng],
                Client_Food: element.name,
                Client_Drinks: element.drinks,
                Client_DrinkPrice: element.drink,
                Client_paidCash: true,
                Client_Key: this.ph.id,
                Client_Total: element.price + parseInt(pay)
              }).then(()=>{
              
              })
             }else{
             this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').push({
                Client_Name: this.ph.name,
                Client_Location: this.gCode.locationName,
                Client_Food: element.name,
                Client_location: [this.cMap.lat, this.cMap.lng],
                Client_Drinks: 'none',
                Client_paidCash: true,
                Client_Total: element.price + parseInt(pay),
                Client_Key: this.ph.id
              }).then(()=>{
              
              })
            }
             quantity.push(element.quantity);
              this.act.getActivityProfile(this.ph.id).update({
                Client_Name: this.ph.name,
                Client_Location: this.gCode.locationName,
                Client_Food: foods,
                Client_location: [this.cMap.lat, this.cMap.lng],
                Client_Drinks: drinks,
                Client_FoodQuantity: quantity,
                Client_Total: this.director.total,
                Client_Key: this.ph.id,
                Delivered: false
         }).then(()=>{
          
         })
         });
        }
    })

    this.navCtrl.push('TrackerPage')

    console.log('trugfh')
  
    this.ph.getUserProfile().child('History').push({
      Name: this.ph.name,
      Location: this.gCode.locationName,
      Food: foods,
      Drinks: drinks,
      FoodQuantity: quantity,
      Total: this.director.total,
      Date: date
    })


 }else{
   console.log('using cash', this.ph.id)
  let modal = this.modalCtrl.create('PaymentPage');
  modal.onDidDismiss(data => {
  
  this.items.forEach(element => {
    console.log(element.name)
  
    if (element.drinks){
     drinks.push(element.drinks);
    }else{
     
     }
     console.log(element.price)
            console.log(element.restuarantKey);
            this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').on('value', snap=>{
              if(snap.val())
              pay = snap.val().Client_Total
            }) 

            if (element.drinks ){
              this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').push({
                Client_Name: this.ph.name,
                Client_Location: this.gCode.locationName,
                Client_Food: element.name,
                Client_location: [this.cMap.lat, this.cMap.lng],
                Client_Drinks: element.drinks,
                Client_DrinkPrice: element.drink,
                Client_paidCash: true,
                Client_Key: this.ph.id,
                Client_Total: element.price
              }).then(()=>{
              
              })
             }else{
             this.ph.getResturantProfile().child(element.restuarantKey).child('Orders').push({
                Client_Name: this.ph.name,
                Client_Location: this.gCode.locationName,
                Client_Food: element.name,
                Client_location: [this.cMap.lat, this.cMap.lng],
                Client_Drinks: 'none',
                Client_paidCash: true,
                Client_Key: this.ph.id,
                Client_Total: element.price
              }).then(()=>{
              
              })
            }
     foods.push(element.name);
     quantity.push(element.quantity);
     console.log(foods, element.drinks)
      this.act.getActivityProfile(this.ph.id).update({
        Client_Name: this.ph.name,
        Client_Location: this.gCode.locationName,
        Client_Food: foods,
        Client_Drinks: drinks,
        Client_Key: this.ph.id,
        Client_Total: this.director.total,
        Client_FoodQuantity: quantity,
        Client_paidCash: true,
        Delivered: false
   }).then(()=>{
  
    })
 });

 console.log('trugfh')

 this.navCtrl.push('TrackerPage')

 this.ph.getUserProfile().child('History').push({
  Name: this.ph.name,
  Location: this.gCode.locationName,
  Food: foods,
  Drinks: drinks,
  FoodQuantity: quantity,
  Total: this.director.total,
  Date: date
})

})
modal.present();
 }
}

}



